.slider {
     width: 100%;
     padding: 20px 10px;
     box-sizing: border-box;
     &__wrapper {
          width: 100%;
          position: relative;
     }

     &__slide {
          width: 100%;
          height: 360px;

          display: flex;
          align-items: center;
          justify-content: center;

          img {
               width: 100%;
               max-height: 100%;
               object-fit: contain;
               border-radius: 20px;
          }
     }


     &__button-prev {
          position: absolute;
          bottom: -30px;
          left: 50px;
          font-size: 52px;
          transition: all 0.3s ease 0s;

          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 20px 40px 15px;
          border-width: 12px 0 12px 33px;
          border-width: 12px 33px 12px 0;
          border-width: 12px 33px 12px 0;
          border-color: transparent #FBFF22 transparent transparent;
          
          &:hover {
               border-color: transparent #848483 transparent transparent;
               transform: scale(1.1);
          }
     }

     &__button-next {
          @extend .slider__button-prev;
          left: 80%;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 12px 0 12px 33px;
          border-color: transparent transparent transparent #FBFF22;

          &:hover {
               transform: scale(1.1);
               border-color: transparent transparent transparent #848483;
          }
     }
}

