.services {
     width: 100%;
     margin: 2em 0;
     
     &__title {
          position: relative;
          text-align: center;
          padding:0 0 10px 0;
     }

     &__list {
          margin-top: 1.5em;
          display: flex;
          align-items: center;
          justify-content: space-around;
     }

     &__item {
          position: relative;
          font-size: 32px;
          padding: 20px 35px 40px 35px;
          background-color: #7772722c;
          border-radius: 15px;
          opacity: .4;
          cursor: pointer;
          transition: all 0.3s ease 0s;
          
          &:hover {
               opacity: 1;
          }

          &::after {
               content: "";
               opacity: 0.3;
               transition: all 0.3s ease 0s;
          }
          
          &.active {
               opacity: 1;
               background-color: #131313;
               &::after {
                    opacity: 1;
                    content: "";
                    display: inline-block;
                    width: 250px;
                    height: 5px;
                    position: absolute;
                    right: 10px;
                    bottom: 5px;
                    background-color: #FBFF22;
                    box-shadow: 3px 0 15px 5px #FBFF22;
                    border-radius: 15px;
               }
          }

          &:first-child {
               span {
                    content: "";
                    display: inline-block;
                    width: 40px;
                    height: 2px;
                    transform: rotate(-45deg) translate(-51px, 0px);
                    position: absolute;
                    right: -50px;
                    top: -100px;
                    background-color: #FBFF22;
                    box-shadow: 3px 0 15px 2px #FBFF22;
               }
          } 

          &:last-child {
               span {
                    content: "";
                    display: inline-block;
                    width: 40px;
                    height: 2px;
                    transform: rotate(45deg) translate(-51px, 0px);
                    position: absolute;
                    left: 50px;
                    top: -30px;
                    background-color: #FBFF22;
                    box-shadow: 3px 0 15px 2px #FBFF22;
               }
     
          } 
          
     }
}

